html, body {
  width: 100%;
  margin: 0;
  padding: 0;
  height: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}
  /* *{
  outline: 1px solid red; 
    }    */


.app {

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: none;

}

.app::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
   background-image: url('/src/assets/images/mainPhoto.jpg');
  background-size: cover;
  background-position: center; 
  z-index: -1;  

}

 .main {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0;
  margin: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  /* overflow-x: hidden;
  overflow-y: auto; */
  box-sizing: border-box;
}



header {
  position:sticky;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  padding: 10px 0;
  z-index: 1000;

}

footer {
  width: 100%;
  padding: 20px;
  background-color: #333;
  color: white;
  text-align: center;
  box-sizing: border-box; 
  
}